import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { ModalService } from '@services/modal.service';
import { UserService } from '@services/modules/user/services/user.service';
import { PlatformService } from '@services/platform.service';
import { LogInService } from '@services/services/log-in.service';
import { MobSignInComponent } from '@shared/components/mobile/mob-sign-in/mob-sign-in.component';
import { Location } from '@angular/common';

/* todo we can remove this module after confirmation mob-auth component */
@Component({
    selector: 'tz-mob-get-started-page',
    templateUrl: './mob-get-started-page.component.html',
    styleUrls: ['./mob-get-started-page.component.scss'],
})
export class MobGetStartedPageComponent {
    public showSignin = true;
    public fromCheckout = false;
    public errorMsg = '';

    constructor(
        private modalService: ModalService,
        private logInService: LogInService,
        private router: Router,
        private userService: UserService,
        private location: Location
    ) {}

    public openSignIn(): void {
        this.userService.openLoginModel().finally(() => {
            this.router.navigate(['search']);
        });
    }

    public openSignUp(loginMailId?: string, fromCheckout?: boolean): void {
        this.modalService.createMainModal(MobSignInComponent, {
            data: { loginMailId, showSignin: false, fromCheckout },
            title: 'ACCOUNT.SIGN.SIGN_UP',
        });
    }

    public loginWithSocialMedia = (media: string) => this.logInService.loginWithSocialMedia(media, this.modalService);

    public onClosePage(): void {
        this.location.back();
    }
}
